<template>
  <div v-if="condition">
    <!--  -->
    <ul class="list">
      <ItemCard
        v-for="item in cardata"
        :key="item.index"
        :car="item"
        :priceSeparated="priceSeparatedNum(item)"
        :bonus="bonus"
        :isRanking="condition.category === 'ranking'"
        @alert="showAlertSales"
      >
      </ItemCard>
    </ul>
  </div>

  <div v-else>
    <!--  -->
    <div v-for="group in cardata" :key="group.index">
      <!-- -->
      <div v-if="group.data.length" class="group-wrp" :id="group.idName">
        <div v-if="group.type !== ''" class="group-name">{{ group.type }}</div>
        <p v-if="group.idName == 'now'" class="m-txt__strong m-txt__normal--center">KINTO<br><br>
PAL21/318/6<br>KINTO<a :href="scheduleFileUrl" target="_blank" rel="noopener"></a></p>
        <div v-for="brand in group.data" :key="brand.index">
          <!--  -->

          <div v-if="brand.carData.length">
            <h3 v-if="hasBrandTitle(group)" class="brand">{{ brand.brand }}</h3>
            <ul class="list">
              <ItemCard
                v-for="item in brand.carData"
                :key="item.index"
                :car="item"
                :priceSeparated="priceSeparatedNum(item)"
                :bonus="bonus"
                :salesType="salesType"
                :isRanking="false"
                @alert="showAlertSales"
              >
              </ItemCard>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import util from "../tools/utility";

export default {
  props: ["cardata", "priceType", "condition", "brandType", "salesType"],
  data() {
    return {
      scheduleFileUrl: this.brandType === "lexus" ? "/assets/file/lexus/l_carchange.pdf": "/assets/file/toyota/t_carchange.pdf"
    }
  },
  computed: {
    bonus() {
      return this.priceType == null
        ? 0
        : parseInt(
            this.priceType.split("_")[1].split("bonus")[1].split("m")[0],
            10
          );
    },
  },
  methods: {
    priceSeparatedNum(val) {
       // separatePrice
      return this.separatePrice(val[this.priceType]);
    },
    separatePrice(num) {
      return String(num,10).replace( /(\d)(?=(\d\d\d)+(?!\d))/g, '$1,');

    },
    showAlertSales(url) {
      //  => 
      this.$emit("alert", url);
    },
    hasBrandTitle(group) {
      let hasBrandTitleFlag = true
      // brandTypeh3
      if (this.brandType !== "") {
        hasBrandTitleFlag = false
      } else {
        // typeMasterselectKeyblandh3
        group.selectOption.forEach((option) => {
          if (option.selectKey == "brand") {
            hasBrandTitleFlag = false
          }
        })
      }
      return hasBrandTitleFlag
    },
  },
};
</script>

