<template>
  <li class="list-item">
    <a href="" class="card card-clm" @click.prevent="goSimulate(linkUrl)">
      <div class="card-clm--child">
        <figure class="card-pic">
          <img v-lazy="car.imagePath" :alt="car.name + car.specialCarType" />
        </figure>
        <p class="card-name">
          <span>{{ car.name }}</span>
          <span v-if="car.specialCarType">{{ car.specialCarType }}</span>
        </p>
      </div>
      <div class="card-clm--child">
        <p v-if="bonus !== 0" class="card-bonus">
          <!--  -->
          <span v-if="bonus === 5">5.5</span
          ><span v-else-if="bonus === 10">11</span><span v-else>16.5</span>
        </p>
        <dl class="card-price">
          <dt></dt>
          <dd>
            <strong>{{ priceSeparated }}</strong
            >()
          </dd>
        </dl>

        <p v-if="salesType == 'dealer' && car.release !== ''" class="card-release">{{ car.release }}</p>

        <p class="card-excomment">
          <!--  -->
          <span v-if="car.ex_comment">{{ car.ex_comment }}</span>
        </p>
      </div>
    </a>
  </li>
</template>

<script>
import itemCardProps from "../mixins/itemCardProps";

export default {
  mixins: [itemCardProps],
  props: ["salesType"],

  computed: {
    linkUrl() {
      // WEBURL
      return  this.noSimulateLink
        ? this.car.linkUrl
        : this.car.brand === ""
        ? "/customer/simulation/lexus/dlr_" + (this.car.car_model_code).toLowerCase() + "/step1"
        : "/customer/simulation/dlr_" + (this.car.car_model_code).toLowerCase() + "/step1";
    },
    noSimulateLink() {
      return this.car.linkUrl.indexOf("customer/") < 0
    }
  },
  methods: {
    goSimulate(url) {
      // 
      if (this.salesType === "dealer") {
        //  => 
        this.$emit("alert", url);
      } else {
        // WEB => 
        location.href = url;
      }
    },
  }
};
</script>

<style lang="scss" scoped>
@import "../../../../style/contents/lineup/_card";

.card-release {
  font-weight: bold;
  color: #f74850;
  margin-bottom: 0;
}

</style>
