import deliveryDate from "../../../../json/deliverydate.json";

export default {
  /**
   *  
   * @param {Array} data
   * @return {Array}
   */
  addStatus: (data) => {
    const deliveryDateMaster = deliveryDate.data;
    return data.map((d) => {
      // 
      if (deliveryDateMaster) {
        const dateVal = deliveryDateMaster.find((e) => {
          return d.car_model_code === e.car_model_code;
        });
        // =>""
        d.deliveryDateComment = dateVal && dateVal.deliveryDateComment ? dateVal.deliveryDateComment : d.brand !== "" ? "" : "";
      }

      d.imageTag = [];
      // 
      if (d.new_product === "1") {
        d.imageTag.push("new");
      }
      // 
      if (d.ranking_now !== "") {
        d.imageTag.push("rank" + d.ranking_now);
      }
      return d;
    });
  },

  /**
   * 
   * @return {String}
   */
  lastUpdate: () => {
    return deliveryDate.lastUpdate || "";
  },
};
