<template>
  <section class="l-cnt__main m-lineup_cnt" :id="salesType">
    <LineupHeader
      :brandType="brandType"
      :salesType="salesType"
    ></LineupHeader>

    <!-- / -->
    <LineupRefiner v-if="originDataNum"
      :term="term"
      :bonus="bonus"
      :brandType="brandType"
      :salesType="salesType"
      @selectTerm="selectTerm"
      @selectBonus="selectBonus"
    >
    </LineupRefiner>

    <div v-if="originDataNum && brandType !== 'lexus'" class="m-clm__cnt result-clm">
      <div class="m-clm__child result-desc">
        <!--  -->
        <ResultDescription
          :num="resultCarNum"
          :condition="condition"
          :tags="statusTag"
        >
        </ResultDescription>
      </div>
    </div>

    <!--  -->
    <LineupResult
      :term="term"
      :bonus="bonus"
      :condition="condition"
      :brandType="brandType"
      :salesType="salesType"
      :exclusion="exclusion"
      :pickup="pickup"
      @dataNum="dataNum"
      @resultNum="resultNum"
    ></LineupResult>

    <ul class="m-list__caption" v-if="originDataNum">
      <li></li>
      <li></li>
      <!-- <li>{{
          deliveryLastUpdate
        }}<br />
        
      </li> -->
    </ul>
  </section>
</template>

<script>
import ScrollHash from "../../../module/ScrollHash";

import LineupRefiner from "../components/LineupRefiner";
import ResultDescription from "../components/ResultDescription";
import LineupResult from "../components/LineupResult";
import LineupHeader from "../components/LineupHeader";

import deliverydate from "../tools/deliverydate";

export default {
  components: {
    LineupRefiner,
    ResultDescription,
    LineupResult,
    LineupHeader,
  },
  props: [
    "brandType",
    "salesType",
    "exclusion",
    "pickup",
    "termDefault",
    "bonusDefault",
  ],
  data() {
    return {
      term: this.termDefault,
      bonus: this.bonusDefault,
      deliveryLastUpdate: deliverydate.lastUpdate(),
      originDataNum: 0,
      resultCarNum: 0,
      condition: null,
      cleared: false,
    };
  },
  computed: {
    statusTag() {
      // 
      const res = [];
      const cdt = this.condition;
      if (!cdt) {
        // no condition
      } else if (!Array.isArray(cdt.option)) {
        res.push(cdt.option.tag);
      } else {
        cdt.option.map((d) => {
          res.push(d.tag);
        });
      }
      return res;
    },
  },
  methods: {
    setCondition(val) {
      // 
      this.condition = val;
      this.cleared = false;
      // GTM 
      this.gAnalytics({
        event: "select_" + val.category,
        label: val.title,
        value: this.statusTag,
      });
    },
    clearCondition() {
      // 
      this.condition = null;
      this.cleared = true;
    },
    selectTerm(val) {
      // GTM 
      this.gAnalytics({
        event: "price_subscriptionTerm",
        label: "()",
        value: val.label,
      });
      this.term = val.num;
    },
    selectBonus(val) {
      // GTM 
      this.gAnalytics({
        event: "price_bonus",
        label: "",
        value: val.label,
      });
      this.bonus = val.num;
    },
    dataNum(val) {
      this.originDataNum = val;
    },
    resultNum(val) {
      this.resultCarNum = val;
    },
    gAnalytics(val) {
      if (typeof window.dataLayer !== "undefined") {
        window.dataLayer.push({
          event: "lineup_" + val.event,
          select_label: val.label,
          select_value: val.value,
        });
      }
    },
  },
  mounted() {
    // hash
    new ScrollHash();
  },
};
</script>

