import brandMaster from "../../../../json/brandMaster.json";

export default {
  /**
   * /
   * @param  {Array} data
   * @param  {String} priceType
   * @return {Array}
   */
  price: (data, priceType) => {
    // =0
    const _data = data.filter((d) => {
      return d[priceType] && parseInt(d[priceType], 10) > 0;
    });
    return _data.slice().sort((a, b) => {
      return a[priceType] - b[priceType];
    });
  },
  /**
   * brandMaster
   * @param  {Array} data
   * @return {Array}
   */
  brand: (data) => {
    // 
    const brandMap = brandMaster.map((d) => {
      return d.brand;
    });
    return data.slice().sort((a, b) => {
      const nameA = brandMap.indexOf(a.brand);
      const nameB = brandMap.indexOf(b.brand);
      return nameA - nameB;
    });
  },
  /**
   * 
   * @param  {Array} data
   * @param  {Array} arr
   * @return {Array}
   */
  list: (data, arr) => {
    const res = [];
    arr.forEach((d) => {
      const e = data.find((f) => {
        const key = Object.keys(d)[0];
        return f[key] === d[key];
      });
      if (e) {
        res.push(e);
      }
    });
    return res;
  },
  /**
   * 
   * @param  {Array} data
   * @param  {String} option
   * @return {Array}
   */
  ranking: (data, option) => {
    return data.slice().sort((a, b) => {
      const numA = parseInt(a[option], 10);
      const numB = parseInt(b[option], 10);
      return numA - numB;
    });
  },
};
