import typeMasterAll from "../../../../json/typeMaster.json";
import typeMasterLexus from "../../../../json/typeMasterLexus.json";
import brandMaster from "../../../../json/brandMaster.json";

/**
 * 1
 * @param {Array} data
 * @return {Array}
 */
function flattenArray(data) {
  return data.reduce((acc, val) => acc.concat(val), []);
}

/**
 * brand
 * @param {Array} data
 * @return {Array}
 */
function groupByBrand(data) {
  const res = [];
  brandMaster.map((thisBrand) => {
    const carArr = data.filter((d) => {
      return d.brand === thisBrand.brand;
    });
    if (carArr.length) {
      res.push({
        brand: thisBrand.brand,
        carData: carArr,
      });
    }
  });
  return res;
}

/**
 * 
 * @param {Array} data
 * @return {Array}
 */
function sortCarSeries(data) {
  let res = [];
  data.map((d) => {
    // res
    const hasListed = res.find((e) => {
      return e.car_model_code == d.car_model_code;
    });
    if (!hasListed) {
      // 
      res.push(d);
      // 
      const series = data.filter((e) => {
        return e.name === d.name && e.car_model_code !== d.car_model_code;
      });
      if (series.length) {
        // 
        res = res.concat(series);
      }
    }
  });
  return res;
}

/**
 * typeMaster
 * @param {Array} data
 * @param {Array} masterOrigin
 * @return {Array}
 */
function sortByTypeMaster(data, masterOrigin) {
  const res = [];
  masterOrigin.forEach((typeGrp) => {
    res.push(
      data.find((d) => {
        return typeGrp.type === d.type;
      })
    );
  });
  return res;
}

/**
 * priority: {num + 1} 
 * @param {Array} data
 * @param {Array} typeMaster
 * @param {Number} num
 * @return {Array}
 */
function groupSubType(data, typeMaster, num) {
  const res = [];
  const subTypeMaster = typeMaster.filter((type) => {
    return type.priority === num + 1;
  });
  subTypeMaster.map((thisType) => {
    const selectedArr = thisType.selectOption.map((opt) => {
      // priority=>hasListed
      if (!opt.selectValue && opt.exceptValue) {
        // exceptValue
        return data.filter((d) => {
          return opt.exceptValue.indexOf(d[opt.selectKey]) < 0;
        });
      } else {
        // selectValue
        const _selected = data.filter((d) => {
          return opt.selectValue.indexOf(d[opt.selectKey]) >= 0;
        });
        // selectValue
        return _selected.sort((a, b) => {
          return opt.selectValue.indexOf(a[opt.selectKey]) - opt.selectValue.indexOf(b[opt.selectKey]);
        });
      }
    });
    // 1
    const typeArr = flattenArray(selectedArr);
    if (typeArr.length) {
      res.push(typeArr);
    }
  });
  return flattenArray(res);
}

export default {
  /**
   * (/)
   * @param {Array} data
   * @param {Array} brandType
   * @param {Array} specifiedTypes typeMaster.json/typeMasterLexus.jsontypeMaster
   * @return {Array}
   */
  group: (data, brandType, specifiedTypes) => {
    // init
    const dataArr = JSON.parse(JSON.stringify(data));
    dataArr.map((d) => {
      d.hasListed = false;
    });
    // typeMasterpriority
    const typeMaster = specifiedTypes !== undefined ? specifiedTypes : brandType === "lexus" ? typeMasterLexus : typeMasterAll;
    const typeByPriority = JSON.parse(JSON.stringify(typeMaster)).sort(function (a, b) {
      return a.priority - b.priority;
    });

    // 
    const groupByType = typeByPriority.map((thisType) => {
      thisType.data = [];

      const selectedArr = thisType.selectOption.map((opt) => {
        if (!opt.selectValue && opt.exceptValue) {
          // exceptValue
          const _selected = dataArr.filter((d) => {
            if (opt.exceptValue.indexOf(d[opt.selectKey]) < 0 && !d.hasListed) {
              // 
              d.hasListed = true;
              return true;
            }
          });
          return _selected;
        } else {
          // selectValue
          const _selected = dataArr.filter((d) => {
            if (opt.selectValue.indexOf(d[opt.selectKey]) >= 0 && !d.hasListed) {
              // 
              d.hasListed = true;
              return true;
            }
          });
          // selectValue
          return _selected.sort((a, b) => {
            return opt.selectValue.indexOf(a[opt.selectKey]) - opt.selectValue.indexOf(b[opt.selectKey]);
          });
        }
      });
      // 1
      let flatSelectedArr = flattenArray(selectedArr);

      //  => end.
      if (!flatSelectedArr.length) {
        return thisType;
      }

      // priority=1: priority=2type
      if (thisType.priority == 1) {
        flatSelectedArr = groupSubType(flatSelectedArr, typeMaster, 1);
      }

      // ()
      const listedCars = sortCarSeries(flatSelectedArr);
      // brand
      thisType.data = groupByBrand(listedCars);

      return thisType;
    });

    // typeMaster
    const res = sortByTypeMaster(groupByType, typeMaster);
    return res;
  },
  /**
   * 1
   * 
   * @param {Array} data
   * @return {Array}
   */
  flattenGroup: (data) => {
    // data
    const groups = data.map((grp) => {
      const brands = grp.data.map((d) => {
        return d.carData;
      });
      return flattenArray(brands);
    });
    // data
    return flattenArray(groups);
  },
};
