AAgB;ACAhB;;;CDIC;ACUD;;;;;;;CDFC;AC4CD;mCD1CmC;AEdnC;;;CFkBC;AClBD;;;CDsBC;ACRD;;;;;;;CDgBC;AC0BD;mCDxBmC;AAEjC;EACE,iBAAiB;AAAA;AADlB;IAIG,iBAAiB;AAAA;AAIrB;EACE,oBAAa;EAAb,oBAAa;EAAb,aAAa;EACb,8BAAmB;EAAnB,6BAAmB;MAAnB,uBAAmB;UAAnB,mBAAmB;EACnB,qBAAiB;MAAjB,iBAAiB;EACjB,iBAAiB;EACjB,mBAAmB;AAAA;AErCrB;AFgCA;MAQI,iBAAiB;MACjB,iBAAiB;AAAA;AAEpB;AAED;EACE,mBAAmB;AAAA;AAKrB;EACE,oBAAa;EAAb,oBAAa;EAAb,aAAa;EACb,mBAAe;MAAf,eAAe;AAAA;AAFhB;IAMK,kBAAkB;IAClB,YAAY;IACZ,qBAAqB;IACrB,cAAc;AAAA;AATnB;IAcO,aAAa;AAAA;AAOvB;EACE,qBAAqB;EACrB,WAAW;EACX,aAAa;EACb,iBAAiB;EACjB,sBAAsB;EACtB,wBAAgB;UAAhB,gBAAgB;EAChB,qBAAqB;EACrB,sBAAsB;AAAA","file":"ResultDescription.vue","sourcesContent":["@charset \"UTF-8\";\n/*\n* \n* \n*/\n/*\nw-main\nPCSP12px\nwidth100%\nmax-width984 + 14*2 = 1012\n\nFULL\n*/\n/* \n--------------------------------- */\n/*\n* mixin\n* mixin\n*/\n/*\n* \n* \n*/\n/*\nw-main\nPCSP12px\nwidth100%\nmax-width984 + 14*2 = 1012\n\nFULL\n*/\n/* \n--------------------------------- */\n.result-count {\n  font-size: 1.8rem; }\n  .result-count strong {\n    font-weight: bold; }\n\n.result-hdg {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: nowrap;\n  font-size: 1.4rem;\n  font-weight: normal; }\n  @media screen and (min-width: 835px) {\n    .result-hdg {\n      font-size: 1.8rem;\n      font-weight: bold; } }\n\n.result-title {\n  white-space: nowrap; }\n\n.status-list {\n  display: flex;\n  flex-wrap: wrap; }\n  .status-list li:before {\n    position: relative;\n    content: \"/\";\n    display: inline-block;\n    margin: 0 .5em; }\n  .status-list li:first-child:before {\n    display: none; }\n\n.clearbtn {\n  display: inline-block;\n  width: auto;\n  min-width: 0%;\n  font-size: 1.4rem;\n  padding: 0.8rem 1.2rem;\n  box-shadow: none;\n  border-radius: 0.5rem;\n  vertical-align: middle; }\n","/*\n* \n* \n*/\n\n\n// breakPoint\n$breakpoints: ('sm': 'screen and (min-width: 320px)',\n  'md': 'screen and (min-width: 835px)',\n  'xl': 'screen and (min-width: 1001px)',\n) !default;\n\n\n//width\n/*\nw-main\nPCSP12px\nwidth100%\nmax-width984 + 14*2 = 1012\n\nFULL\n*/\n\n$w-full : 100%;\n$w-main : 1008px; //  984px\n$w-mid : 828px; //  816px\n$w-small : 702px; //  690px\n\n$w-padding: 0 12px;\n\n// margin\n$mgn-xl : 60px; // section(PC)\n$mgn-l : 48px; // section mg(PC)\n$mgn-m : 36px; // sectionmg(PC) mg(PC)\n$mgn-s : 24px; //  ->ex)(PC/SP)\n$mgn-ss : 12px; // mg(PC/SP)\n\n//line-height\n$text-height : 1.75;\n$title-height : 1.5;\n\n// \n$txt_size_base: 1.4rem;\n// \n$txt_size_heading01_pc: 4rem;\n$txt_size_heading01_sp: 3rem;\n// \n$txt_size_heading02_pc: 2.8rem;\n$txt_size_heading02_sp: 2.4rem;\n// \n$txt_size_heading03_pc: 2rem;\n$txt_size_heading03_sp: 1.8rem;\n// \n$txt_size_heading04_pc: 1.6rem;\n$txt_size_heading04_sp: 1.6rem;\n\n/* \n--------------------------------- */\n\n//\n$col_primary : #00708d;\n$col_primary2: #69A7BF;\n$col_primary3: #D9F7FF;\n$col_primary4: #7ECBC3;\n\n// \n$col_font_base: #333;\n$white : #fff;\n$gray1: #6B6B6B;\n$gray2: #aaa;\n$gray3: #ccc;\n$gray4: #eaeaea;\n$gray5: #F7F7F7;\n\n//  / \n$col_accent_red: #E53935;\n$col_accent_red_light: #FF6759;\n$col_accent_red_light2: #FFA58F;\n$col_accent_pink: #FFEBEE;\n$col_accent_yellow_dark: #FDD835;\n$col_accent_yellow: #FFEE58;\n$col_accent_yellow_light: #FFF59D;\n$col_accent_yellow_light2: #FFFDE7;\n$col_accent_orange: #EB9500;\n\n// \n$col_link: #009DC5;\n$col_link_hover: #009DC5;\n$opacity_hover: .8;\n\n\n// \n$col_border : $gray4; // eaeaea\n$bggray : #e7e7e7;\n$bglightgray : $gray5; // F7F7F7\n$bgprimary: #e3eff2;\n\n//font-family\n$title : \"Noto Sans JP\", sans-serif;\n$text : '\"\", \" Medium\", \"Yu Gothic Medium\",\" ProN W3\", \"Hiragino Kaku Gothic ProN\", \"Hiragino Sans\",\" W3\", \"\", Meiryo, sans-serif';\n","/*\n* mixin\n* mixin\n*/\n\n@import './variable';\n\n// media Query\n// 835px default\n@mixin mq($breakpoint: md) {\n  @media #{map-get($breakpoints, $breakpoint)} {\n    @content;\n  }\n}\n\n// $device: sp (Default) or pc\n// \n@function getMargin($mg_type, $device: \"sp\") {\n  @if $mg_type=='section' and $device==\"pc\" {\n    @return $mgn-xl;\n  }\n\n  @if $mg_type=='section' and $device==\"sp\" {\n    @return $mgn-l;\n  }\n\n  @if $mg_type=='content' and $device==\"pc\" {\n    @return $mgn-l;\n  }\n\n  @if $mg_type=='content' and $device==\"sp\" {\n    @return $mgn-m;\n  }\n\n  // TODO: PCSP\n  //\n  @if $mg_type=='block' {\n    @return $mgn-m;\n  }\n\n  @if $mg_type=='element' {\n    @return $mgn-s;\n  }\n\n  @if $mg_type=='min' {\n    @return $mgn-ss;\n  }\n  @return 'auto';\n}\n\n// PCSP\n// $margin -> margin-bottom , $mg_type -> section etc...\n// ex) @include setMargin(margin-bottom, 'section');\n@mixin setMargin($margin, $mg_type) {\n  #{$margin}: getMargin($mg_type, \"sp\");\n  @include mq {\n    #{$margin}: getMargin($mg_type, \"pc\");\n  }\n}\n\n// PCSP\n// PC AND SPPC(SP)\n// PC@include mq\n@mixin setMarginPC($margin, $mg_type) {\n  #{$margin}: getMargin($mg_type, \"pc\");\n}\n\n@mixin setMarginSP($margin, $mg_type) {\n  #{$margin}: getMargin($mg_type, \"sp\");\n}\n\n\n@mixin heading($size : 2.8rem , $mb : 4.8rem) {\n  font-family: $title;\n  font-weight: bold;\n  font-size: $size;\n  margin-bottom: $mb;\n  line-height: $title-height;\n}\n\n// @mixin button($bg-color : $primary , $color : $white) {\n//   display: inline-block;\n//   background-color: $bg-color;\n//   color: $color;\n//   border-radius: .5rem;\n//   line-height: $title-height;\n//   cursor: pointer;\n//   position: relative;\n//   text-decoration: none;\n//   transition: all .2s;\n\n//   // \n//   padding: 2rem 1.2rem;\n//   width: 27rem;\n//   font-size:1.6rem;\n// }\n\n@mixin Text($size:1.4rem , $weight: normal,$mb: $mgn-s) {\n  font-weight: $weight;\n  font-size: $size;\n  line-height: $text-height;\n  margin-bottom: $mb;\n}\n\n\n@mixin Width ($m-width : $w-main) {\n  margin: auto;\n  width: 100%;\n  max-width: $m-width;\n  padding: $w-padding;\n  margin-bottom: 60px;\n}\n"]}]);
