import sort from "../tools/sort";
import listup from "../tools/listup";

import salesTypeMaster from "../../../../json/typeMasterSales.json";

export default {
  methods: {
    listupDealerSales(data) {
      /*
        
          1. typeMasterSales.json 
              priority"1"!!
          2. 
              -> 
          3.  > typeMaster.json
              -> prioriry:1priority:2
          4.  > 1
       */

      const brandType = this.brandType;
      const priceType = this.priceType;
      // salesTypeMaster
      const _data = listup.group(data, brandType, salesTypeMaster);
      return _data.map((group) => {
        group.data.map((brand) => {
          // (specialCarType)
          let _brand = sort.price(brand.carData, priceType);
          // 
          _brand = listup.group(_brand, brandType);
          // 1
          brand.carData = listup.flattenGroup(_brand);
          return brand;
        });
        return group;
      });
    },
  }
};
