<template>
  <ul class="m-lineup__selector m-lineup__selector--4col" :data-type="identify">
    <li v-for="val in options" :key="val.num">
      <input
        type="radio"
        :name="'status-bonus' + identify"
        :value="val.num"
        :id="'bonus' + val.num + identify"
        v-model="bonus"
        class="m-lineup__radio"
      />
      <label :for="'bonus' + val.num + identify">{{ val.label }}</label>
    </li>
  </ul>
</template>

<script>
const bonusOptions = [
  {
    num: 0,
    label: "",
  },
  {
    num: 5,
    label: "5.5",
  },
  {
    num: 10,
    label: "11",
  },
  {
    num: 15,
    label: "16.5",
  },
];

export default {
  props: ["identify", "value"],
  data() {
    return {
      options: bonusOptions,
    };
  },
  computed: {
    bonus: {
      get() {
        return this.value;
      },
      set(val) {
        const obj = this.options.find((d) => {
          return d.num === val;
        });
        this.$emit("selectBonus", obj);
      },
    },
  },
};
</script>

