export default {
  /**
   * -
   * @param {Number} num
   * @return {string}  ex. 1000  1,000
   */
  separateNum: (num) => {
    const formatter = new Intl.NumberFormat("ja-JP");
    return formatter.format(Math.round(Number(num)));
  },
  /**
   * 
   * @param  {Array} data
   * @param  {String} option
   * @return {Array}
   */
  addRankingTag: (data, option) => {
    return data.map((d, i) => {
      // 
      const rank = parseInt(d[option], 10);
      const idx = i + 1;
      d.rankingTag = rank > idx ? idx : rank;
      return d;
    });
  },
  /**
   * 
   * @param  {Array} data
   * @param  {Array} option
   * @return {Array}
   */
  addExComment: (data, option) => {
    const dataArr = JSON.parse(JSON.stringify(data));
    return dataArr.map((d) => {
      option.forEach((e) => {
        if (d[e.status] !== "1") {
          d.ex_comment += d.ex_comment !== "" ? "\n" : "";
          d.ex_comment += e.tag + ":" + d[e.status];
        }
      });
      return d;
    });
  },
};
