import Vue from "vue";
import CarLineup from "./views/CarLineup.vue";
import ItemCardDefault from "./components/ItemCardDefault.vue";
import VueLazyload from "vue-lazyload";


document.addEventListener("DOMContentLoaded", () => {
  Vue.component("ItemCard", ItemCardDefault);
  Vue.use(VueLazyload);

  new Vue({
    el: "#carlineup",
    components: { CarLineup },
    render: function (createElement) {
      return createElement("CarLineup", {
        props: {
          brandType: this.$el.getAttribute('data-brand-type') || "",
          salesType: this.$el.getAttribute("data-sales-type"),
        },
      });
    },
  });

  new Vue({
    el: "#salesCarlineup",
    components: { CarLineup },
    render: function (createElement) {
      return createElement("CarLineup", {
        props: {
          brandType: this.$el.getAttribute('data-brand-type') || "",
          salesType: this.$el.getAttribute("data-sales-type"),
        },
      });
    },
  });
});
