<template>
  <ul class="m-lineup__selector m-lineup__selector--3col">
    <li v-for="val in options" :key="val.num">
      <input
        type="radio"
        :name="'status-term' + identify"
        :id="val.num + 'year' + identify"
        :value="val.num"
        v-model="term"
        class="m-lineup__radio"
      />
      <label :for="val.num + 'year' + identify">{{ val.label }}</label>
    </li>
  </ul>
</template>

<script>
const termOptions = [
  {
    num: 3,
    label: "3",
  },
  {
    num: 5,
    label: "5",
  },
  {
    num: 7,
    label: "7",
  },
];

export default {
  props: ["identify", "value"],
  data() {
    return {
      options: termOptions,
    };
  },
  computed: {
    term: {
      get() {
        return this.value;
      },
      set(val) {
        const obj = this.options.find((d) => {
          return d.num === val;
        });
        this.$emit("selectTerm", obj);
      },
    },
  },
};
</script>
