export default {
  /**
   * 
   * @param {Array} data
   * @return {Array}
   */
  viewFlag: (data) => {
    return data.filter((d) => {
      if (d.viewFlag == "") {
        return false;
      } else {
        return true;
      }
    });
  },
  /**
   * 
   * @param {Array} data
   * @param {String} brand
   * @return {Array}
   */
  brandType: (data, brand) => {
    if (!brand) {
      return data;
    } else {
      const brandJp = brand === "toyota" ? "" : "";
      return data.filter((d) => {
        if (d.brand === brandJp) {
          return true;
        }
      });
    }
  },
  /**
   * 
   * @param {Array} data
   * @param {Array} condition 
   * @param {boolean} exclusion 
   * @return {Array}
   */
  filter: (data, condition, exclusion) => {
    const findCdt = (d) => {
      return condition.find((cdt) => {
        const key = Object.keys(cdt)[0];
        const val = cdt[key];
        if (d[key] === undefined) {
          d[key] = "";
        }
        return d[key] === val;
      });
    };

    let res;
    if (exclusion) {
      // 
      res = data.filter((d) => {
        const _res = findCdt(d);
        return !_res;
      });
    } else {
      res = data.filter((d) => {
        const _res = findCdt(d);
        return _res !== undefined;
      });
    }

    return res;
  },
  /**
   * 
   * @param {Array} data
   * @param {Array} condition ,min,max
   * @param {boolean} equal <= >= 
   * @return {Array}
   */
  between: (data, condition, equal) => {
    const key = condition[0];
    const min = parseInt(condition[1], 10);
    const max = parseInt(condition[2], 10);
    return data.filter((d) => {
      if (equal) {
        return min <= parseInt(d[key], 10) && parseInt(d[key], 10) <= max;
      } else {
        return min < parseInt(d[key], 10) && parseInt(d[key], 10) < max;
      }
    });
  },
};
